/**
* CSS related to front-end.
*
* NOTES:
* - Each class should start with "wp-pf-" to avoid conflicts with other CSS.
*/

@media only screen and (max-width: 600px) {
    .wp-pf-hide-on-mobile {
        display: none !important;
    }
}

@media only screen and (min-width: 601px) and (max-width: 1024px) {
    .wp-pf-hide-on-tablet {
        display: none !important;
    }
}

@media only screen and (min-width: 1025px) {
    .wp-pf-hide-on-desktop {
        display: none !important;
    }
}

